import { TaskDetailPage } from './../pages/task-detail/task-detail';
import { RespondentChatPage } from './../pages/respondent-chat/respondent-chat';
import { OtpPage } from './../pages/otp/otp';
import { GroupChatPage } from './../pages/group-chat/group-chat';
import { MessagesPage } from './../pages/messages/messages';
import { ParticipantsPage } from './../pages/participants/participants';
import { TasksPage } from './../pages/tasks/tasks';
import { ConfirmIdentityPage } from './../pages/confirm-identity/confirm-identity';
import { AuthenticationProvider } from './../providers/authentication/authentication';
import { ManageProfilePage } from './../pages/manage-profile/manage-profile';
import { LanguageSelectionPage } from './../pages/language-selection/language-selection';
import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { SharedProvider } from '../providers/shared/shared.provider';
import { UnitsPage } from '../pages/units/units';
import { ProfilePage } from '../pages/profile/profile';
import { ApiProvider } from '../providers/api/api.provider';
import { ManageTaskPage } from '../pages/manage-task/manage-task';

import { OneSignal, OSNotificationPayload } from '@ionic-native/onesignal';
import { isCordovaAvailable } from '../common/is-cordova-available';
import { oneSignalAppId, sender_id } from '../config';

import { TermsPage } from '../pages/terms/terms';
import { Storage } from '@ionic/storage';
import { TaskPreviewPage } from '../pages/task-preview/task-preview';
@Component({
  templateUrl: 'app.html',
  providers: [SharedProvider]
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  rootPage: any;
  user: any = {};
  unitName: string = null;
  public appState: any = {};
  pages: Array<{ title: string, component: any }>;
  constructor(private _platform: Platform, private storage: Storage, public _statusBar: StatusBar, private oneSignal: OneSignal, public _splashScreen: SplashScreen, public auth: AuthenticationProvider, private _shared: SharedProvider, private _api: ApiProvider) {
    this.initializeApp();
    this.cacheApplicationData();
    _platform.ready().then(() => {
      if (_platform.is('ios')) {
        _statusBar.styleLightContent();
        _statusBar.overlaysWebView(false);
      }
      _statusBar.styleDefault();
      _statusBar.backgroundColorByHexString('#e5e5e5');
      setTimeout(() => {
        _splashScreen.hide();
      }, 500);
    });
  }

  initializeApp() {
    this._platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this._statusBar.styleDefault();
      if (isCordovaAvailable()) {
        this.oneSignal.startInit(oneSignalAppId, sender_id);
        this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.Notification);
        this.oneSignal.handleNotificationReceived().subscribe(data => this._onPushReceived(data.payload));
        this.oneSignal.handleNotificationOpened().subscribe(data => this._onPushOpened(data.notification.payload));
        this.oneSignal.endInit(); 
      }
    });
    setTimeout(() => {
      this._splashScreen.hide();
    }, 300);
  }
  private _onPushReceived(payload: OSNotificationPayload) {
    console.log('Push recevied:' + payload.body);
  }

  private _onPushOpened(payload: OSNotificationPayload) {
    console.log('Push opened: ' + payload.body);
  }
  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
  async cacheApplicationData() {
    let data = await this._shared.LS.get('appState');
    if (!data) {
      this._shared.Loader.show();
      this._api.getTranslation().subscribe(response => {
        this.appState.translation = response.translation;
        this._shared.LS.set('appState', this.appState);
        this.auth.cacheTranslation(response.translation);
        this._shared.Loader.hide();
      }, err => {
        console.log(err);
        this._shared.Loader.hide();
      });
    } else {
      this.auth.cacheTranslation(data.translation);
    }
    if (!data || !data.profileCompleted) {
      this.rootPage = LanguageSelectionPage;
    } else {
      this.auth.cacheTranslation(data.translation);
      this.auth.cacheToken(data.user.token);
      this.auth.cacheUserData(data.user);
      this.auth.cacheUnitId(data.unitId);
      this.auth.cacheUnitName(data.unitName, data.userUnitRole);
      this.auth.cacheLanguage(data.language)

      this.auth.cacheProfileCompletedState(data.profileCompleted);
      if (data.profileCompleted) {
        this.rootPage = data.unitId ? TasksPage : UnitsPage;
      } else {
        this.rootPage = data.user.dob ? ConfirmIdentityPage : ManageProfilePage;
      }
    }

  }
  viewPage(pageName) {
    if (pageName == 'participants') {
      this.nav.push(ParticipantsPage);
    } else if (pageName == 'messages') {
      if (this.auth.userUnitRole == 4) {
        this.nav.push(RespondentChatPage, { id:this.auth.user.id});
      } else {
        this.nav.push(MessagesPage);
      }
     
    } else if (pageName == 'profile') {
      this.nav.push(ProfilePage, { user_id: this.auth.user.id, page: 'profile' });
    } else if (pageName == 'unit') {
      this.nav.push(UnitsPage);
    } else if (pageName == 'terms') {
      this.nav.push(TermsPage, { source: 'sidemenu' });
    }
  }
}



