import { AuthenticationProvider } from './../../providers/authentication/authentication';
import { RegistrationPage } from './../registration/registration';
import { ApiProvider } from './../../providers/api/api.provider';
import { Component } from '@angular/core';
import { NavController, Platform, NavParams } from 'ionic-angular';
import { SharedProvider } from '../../providers/shared/shared.provider';


@Component({
  selector: 'page-terms',
  templateUrl: 'terms.html',
})
export class TermsPage {
  public terms: any;
  public source: any;
  constructor(private _navParam: NavParams,  private _platform: Platform, private _navCtrl: NavController, private _shared: SharedProvider, private _api: ApiProvider, private _auth: AuthenticationProvider) {
    this.source = this._navParam.get('source');
  }
  ionViewDidLoad() {
    this.getTerms();
  }
  async getTerms() {
    this._shared.Loader.show();
    this._api.getTerms({ languageId: this._auth.language }).subscribe(response => {
      this.terms = response['tc'][this._auth.language];
      this._shared.Loader.hide();
    }, err => {
      console.error(err);
      this._shared.Loader.hide();
    });
  }
  disagree() {
    this._platform.exitApp();
  }
  agree() {
    this._navCtrl.push(RegistrationPage)
  }
} 



