import { Component } from '@angular/core';
import { NavController, NavParams, ViewController } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api.provider';
import { SharedProvider } from '../../providers/shared/shared.provider';
 
@Component({
  selector: 'page-response',
  templateUrl: 'response.html',
})
export class ResponsePage {
  public response:any = {}; 
  public submitted:boolean = false; 
  public segment: any;
  constructor(private _shared: SharedProvider, private _api: ApiProvider, private _navCtrl: NavController, private _navParams: NavParams, private _viewCtrl: ViewController) {
    this.segment = this._navParams.get('segment');
    this.response = this._navParams.get('data');
  }

  ionViewDidLoad() {
    
  }
  dismiss() {
    this._viewCtrl.dismiss({refresh: false});
  }
  submit() {
    if (!this.response.description) {
      this._shared.Toast.show('Please enter your comment.');
      return false;
    } 
    if (this.segment == 'response') {
      this.addResponse();
    } else {
      this.addReaction();
    }
  }
  addResponse() { 
    this._shared.Loader.show();
    this._api.insertResponse(this.response).subscribe(response => {
      this._shared.Loader.hide();
      if (response.id) {
        this._shared.Toast.show('Your response has been added successfully.');
        this._viewCtrl.dismiss({refresh: true});
      } else {
        this._shared.Toast.show('Something went wrong. Please try again.');
      }
    }, err => {
      this._shared.Loader.hide();
      console.error(err);
    })
  }
  addReaction() { 
    this._shared.Loader.show();
    this._api.insertReaction(this.response).subscribe(response => {
      this._shared.Loader.hide();
      if (response.id) {
        this._shared.Toast.show('Your reaction has been added successfully.');
        this._viewCtrl.dismiss({refresh: true});
      } else {
        this._shared.Toast.show('Something went wrong. Please try again.');
      }
    }, err => {
      this._shared.Loader.hide();
      console.error(err);
    })
  }
}



