import { SharedProvider } from './../../providers/shared/shared.provider';
import { ApiProvider } from './../../providers/api/api.provider';
import { Component } from '@angular/core';
import { ViewController, NavParams } from 'ionic-angular';


@Component({
  selector: 'page-search-users',
  templateUrl: 'search-users.html',
})
export class SearchUsersPage {
  public loadingData: boolean = false;
  public autocomplete: any = {};
  public users: any;
  public source: string = null;
  private selectedUsers: any = [];
  constructor(private _viewCtrl: ViewController, private _shared: SharedProvider, private _api: ApiProvider, private _navParams: NavParams) {
    this.source = this._navParams.get("source");
    if( this.source == 'task-detail') {
      this.users = this._navParams.get("users");
   }
   }
  ionViewDidLoad() {
    if( this.source == 'manage-task') {
      this.getUsers();
    }
   }
  getUsers() {
    this._shared.Loader.show();
    this._api.getUnitUsers().subscribe(data => {
      this.users = data.getUnitUser;
      this._shared.Loader.hide();
    }, err => {
      console.error(err);
      this._shared.Loader.hide();
    });
  } 
  selectUser(user) {
    this.selectedUsers.push(user);
  }
  done() {
   this.source == 'manage-task' ? this._viewCtrl.dismiss({ users: this.selectedUsers }) : this._viewCtrl.dismiss();
  }
  dismiss() {
    this._viewCtrl.dismiss();
  }
  updateSearch() {

  }


}



