import { Injectable } from '@angular/core';
import { SharedProvider } from '../shared/shared.provider';
@Injectable()
export class AuthenticationProvider {
  public token: string = null;
  public unitId: number;
  public user: any = {};
  public unitName: string = null;
  public userUnitRole: any = null;
  public profileCompleted: boolean;
  public language: string = 'English';
  public translation: any = {};
  constructor(private _shared: SharedProvider) { }
  cacheUserData(data) {
    this.user = data;
  }
  cacheToken(token: string) {
    this.token = token;
  }
  cacheUnitId(unitId) {
    this.unitId = unitId;
  }
  cacheUnitName(unitName, userUnitRole) {
    this.unitName = unitName;
    this.userUnitRole = userUnitRole;
  }
  cacheProfileCompletedState(value) {
    this.profileCompleted = value;
  }
  cacheLanguage(selectedLanguage) {
    this.language = selectedLanguage;
  }
  cacheTranslation(data) {
    this.translation = data;
  }
  translateData(value) {
    value = value.trim();
    let key = value.toLowerCase().split(' ').join('_');
    if (Object.keys(this.translation).length > 0 && this.language) {
      if (typeof this.translation[this.language][key] == 'undefined') {
        return value;
      } else {
        return this.translation[this.language][key];
      }
    } else {
      return value;
    }
  }
}



