import { Component, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { Events, Content } from 'ionic-angular';
import { ApiProvider } from './../../providers/api/api.provider';

import { Socket } from 'ng-socket-io';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { SharedProvider } from '../../providers/shared/shared.provider';
import { Observable } from 'rxjs/Observable';
 
@Component({
  selector: 'page-respondent-chat',
  templateUrl: 'respondent-chat.html',
})
export class RespondentChatPage {
 
  @ViewChild(Content) content: Content;
  @ViewChild('chat_input') messageInput: ElementRef;

  public title: any;
  public messages = [];
  public fromId: number;
  public unitId: number;
  public userName:string;
  public userId:number;
  public message:any;
  public fullName:string;
  public toId:number;
  public roleId:number;
  public room:string;
  

  
  constructor(public navCtrl: NavController,public socket:Socket ,public navParams: NavParams, private _api: ApiProvider, private shared: SharedProvider, private _auth:AuthenticationProvider,  private _viewCtrl: ViewController) {
   // console.log('respondent id');
    this.fromId = this.navParams.get('id');
    //console.log(this.navParams.get('id'));
    this.unitId = this._auth.unitId
    this.userId = this._auth.user.id;
    this.fullName = this._auth.user.full_name;
    this.roleId=this._auth.userUnitRole;
   
    if(this.roleId == 4) {
      this.room=  this.unitId+"_"+this.userId;
      this.toId = 0
    } else {
      this.toId = this.fromId
      this.room=  this.unitId+"_"+this.toId;
    }
    console.log('room');
    console.log(this.room);
console.log('room');
    this.getMessages().subscribe(message => {
      console.log('@@@@@@@@@@@@@@@@@@@@@@@@@@@@@2');
      console.log(message);
 console.log('@@@@@@@@@@@@@@@@@@@@@@@@@@@@@2');

      this.messages.push(message);
  // this.content.scrollBottom();

    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RespondentChatPage');

  }
  

  sendMessage() {
    console.log('inside send');
  
    
    var currentdate = new Date(); 
    var datetime =currentdate;
    this.socket.emit('add-message', { msg: this.message,room:this.room ,unit_id: this.unitId, from_id: this.userId,to_id:this.toId,created_at: datetime,full_name:this.fullName });
              let data = { msg: this.message,room:this.room , unit_id: this.unitId, from_id: this.userId,to_id:this.toId,created_at: datetime,full_name:this.fullName };
        this._api.sendChat(data)
          .subscribe(res => {
            if (res['status'] == 'success') {
              console.log('in sucess');
             
            } 
          }, err => {
            console.log(err);
          
          });

   this.message = '';
  //  this.content.scrollBottom();
  this.scrollToBottom();

  //this.message
  }



  getMessages() {
   // if (this.message) {

   
    let observable = new Observable(observer => {
      this.socket.on('message', (data) => {
     console.log('obserable');
     console.log(data);
     console.log('obserable');
       // if(data.room == this.room){
        observer.next(data);
       // }
      });
    })
    return observable;
  //}
 // this.scrollToBottom();

  }
  scrollToBottom() {
    setTimeout(() => {
      if (this.content.scrollToBottom) {
        this.content.scrollToBottom();
      }
    }, 400)
  }
  ngOnInit() {

    this.socket.connect();

    let data = { from_id: this.userId,to_id:this.toId, unit_id: this.unitId };
    console.log(data);
    this._api.getRespondentChat(data)
      .subscribe(res => {
        if (res['status'] == 'success') {
          this.messages = res.chat;
          this.userName = res.username;
        }
        this.shared.Loader.hide();
      }, err => {
        this.shared.Loader.hide();
      });
      this.scrollToBottom();


  }

  
ionViewWillLeave() {
  this.socket.disconnect();
}
dismiss() {
  this._viewCtrl.dismiss();
}
}



