export const baseURL = 'https://dragonflyuat.infini.work/api';
 //export const baseURL = 'http://192.168.2.98:8080/api';  
import { Injectable } from '@angular/core';

@Injectable()
export class TokenProvider {
  public token: any;
  public user: any = {};
  public isTaskAdded: any = null;
  constructor() { }
  setAuthData(data) {
    try {
      this.user = JSON.parse(data);
    } catch (e) {
      this.user = data;
    }
  }
  dropAuthData() {
    this.token = null;
    this.user = null;
  }
  toggleTaskAdded(id?) {
    this.isTaskAdded = id || null;
  }
}

