import { ApiProvider } from './../../providers/api/api.provider';
import { Component } from '@angular/core';
import { NavController, NavParams, Platform } from 'ionic-angular';
import { SharedProvider } from '../../providers/shared/shared.provider';
import { AuthenticationProvider } from './../../providers/authentication/authentication';
import { IdentityFoundPage } from './../identity-found/identity-found';
import { UnitsPage } from './../units/units';
import { ManageProfilePage } from '../manage-profile/manage-profile';
import { Storage } from '@ionic/storage';
import { OneSignal } from '@ionic-native/onesignal';

@Component({
  selector: 'page-otp',
  templateUrl: 'otp.html'
})
export class OtpPage {

  public submitted: boolean = false;
  public otpForm: any = {};
  public invalidOtp: boolean = false;
  public mobileNumber: number;
  public appState: any = {};
  private _oneSingleIds: any = { pushToken: null, deviceId: null };
  public pushToken: any;
  public deviceId: any;
  constructor(private _platform: Platform, private _oneSignal: OneSignal, private _navCtrl: NavController, private storage: Storage, private _navParams: NavParams, private _shared: SharedProvider, private _api: ApiProvider, private _authentication: AuthenticationProvider) {
    this.otpForm.mobile = this._navParams.get("mobile");
    this.otpForm.uid = this._navParams.get("id");
    this.otpForm.validotp = this._navParams.get("otp");
    this.initDeviceToken();
  }


  initDeviceToken() {
    this._platform.ready().then(() => {
      if (this._platform.is('cordova')) {
        this._oneSignal.getIds().then(identity => {
          this._oneSingleIds.pushToken = identity.pushToken;
          this._oneSingleIds.deviceId = identity.userId;
          this.storage.set('pushToken', identity.pushToken);
          this.storage.set('deviceId', identity.userId);
        });
      }
    });
  }
  ionViewDidLoad() {
    console.log(this.otpForm);
  }
  back() {
    this._navCtrl.pop();
  }
  submit(data) {
    this.submitted = true;
    if (!data.valid) {
      this._shared.Toast.show(this._authentication.translateData('Please fill all mandatory fields'));
      return
    } else {
      this.submitted = false;
      this._shared.Loader.show();
      this._api.checkOtp(this.otpForm)
        .subscribe(response => {
          this._shared.Loader.hide();
          if (response.status == 0) {
            this.invalidOtp = true;
            this._shared.Toast.show(response.msg);
          } else {
            this.appState.user = response.user;
            this._shared.LS.get('appState').then((data: any) => {
              if (data) {
                data.user = response.user;
                this._shared.LS.set('appState', data);

                if (this._oneSingleIds.deviceId) {
                  let data1 = { 'deviceId': this._oneSingleIds.deviceId, 'pushToken': this._oneSingleIds.pushToken, 'userId': data.user.id }
                  this._api.registerDevice(data1)
                    .subscribe(res => {
                      console.log(res);
                    }, err => {
                      console.error(err);
                    }); 
                } 
              } else {
                this.appState.user = response.user;
                this._shared.LS.set('appState', this.appState);
              }
              this._authentication.cacheToken(response.user.token);
              this._authentication.cacheUserData(response.user);
              if (response.user.dob) {
                this._navCtrl.push(IdentityFoundPage);
              } else {
                this._navCtrl.push(ManageProfilePage);
              }
            });
          }
        }, err => {
          console.info(err);
          this._shared.Loader.hide();
        });
    }
  }
  resendOtp() {
    this._shared.Loader.show();
    this._api.resendOtp({ mobile: this.otpForm.mobile })
      .subscribe(response => {
        this._shared.Loader.hide();
        console.log(response);
        if (response.status == 'success') {
          this.otpForm.validotp = response.otp;
          this._shared.Toast.show(this._authentication.translateData('OTP Resent successfully'));
        }
      }, err => {
        console.info(err);
        this._shared.Loader.hide();
      });
  }

}



