import { TokenProvider } from './../../providers/config';
import { ManageTaskPage } from './../manage-task/manage-task';
import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, AlertController, FabContainer, ActionSheetController, Content } from 'ionic-angular';
import { SharedProvider } from '../../providers/shared/shared.provider';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { ApiProvider } from './../../providers/api/api.provider';
import { UnitsPage } from '../units/units';
import { TaskDetailPage } from '../task-detail/task-detail';


@Component({
  selector: 'page-tasks',
  templateUrl: 'tasks.html',
})
export class TasksPage {
  public tasks: any = [];
  public selectedUnit: string = null;
  public listState: any = { sortBy: 'ddatetime', page: 1, filter: { status: [1] } };
  public sorting = [{ label: 'Status', value: 'status' }, { label: 'Due Date', value: 'ddatetime' }, { label: 'Expiry Date', value: 'edatetime' }];
  public filter = [{ label: 'Open', value: 1 }, { label: 'Schedule', value: 2 }, { label: 'Closed', value: 3 }, { label: 'Completed', value: 4 }, { label: 'Cancelled', value: 5 }, { label: 'Expired', value: 6 }];
  public pagingEnabled: boolean = true;
  public userRole: any;
  @ViewChild(Content) content: Content;
  constructor(private _navCtrl: NavController, private _shared: SharedProvider, private _api: ApiProvider, private _auth: AuthenticationProvider, private _alertCtrl: AlertController, private _actionSheetCtrl: ActionSheetController, private _config: TokenProvider) {
  }
  ionViewDidLoad() {
    this.userRole = this._auth.userUnitRole;
    this.selectedUnit = this._auth.unitName;
    this.getTasks();
  }
  ionViewWillEnter() {
    console.log(this._config.isTaskAdded);
    if (this._config.isTaskAdded) {
      this.addNewTask(this._config.isTaskAdded);
      this._config.toggleTaskAdded(null);
    }
  }
  addNewTask(id) {
    this._shared.Loader.show();
    this._api.getTaskDetails({ task_id: id, role_id: this._auth.userUnitRole }).subscribe(response => {
      if (response.status == 1) {
        response.tasks[0]['isNew'] = true;
        response.tasks[0]['status_name'] = 'New';
        this.tasks.unshift(response.tasks[0]);
        console.log(this.tasks);
        this._scrollToTop();
      }
      this._shared.Loader.hide();
    }, err => {
      console.error(err);
      this._shared.Loader.hide();
    });
  }

  private _scrollToTop() {
    this.content.scrollToTop();
  }
  getTasks(refresher?, sort?) {
    if (!refresher) {
      this._shared.Loader.show();
    }
    if (sort) {
      this.listState.sortBy = sort;
      this._scrollToTop();
    }
   this._api.getTaskList(this.listState).subscribe(response => {
      if (response) {
        this.tasks = response.tasks;
      } else {
        console.log(response.msg);
      }
      this._shared.Loader.hide();
      if (refresher) {
        refresher.complete();
      }
    }, err => {
      console.log(err);
      this._shared.Loader.hide();
      if (refresher) {
        refresher.complete();
      }
    });
  }
  doInfinite(infiniteScroll: any) {
    this.listState.page = this.listState.page + 1;
    this._api.getTaskList(this.listState).subscribe(data => {
      if (data.tasks.length) {
        data.tasks.forEach(element => {
          this.tasks.push(element);
        });
      } else {
        this.pagingEnabled = false;
      }
      infiniteScroll.complete();
    }, err => {
      console.error(err.status);
      this._shared.Toast.show('Oops! something went wrong');
    });
  }
  refreshTask(refresher) {
    this.listState.page = 1;
    this.getTasks(refresher);
  }
  switchUnit() {
    this._navCtrl.push(UnitsPage);
  }
  getRespondents(value) {
    if (value.length > 0) {
      if (value.length > 4) {
        return `${value[0].full_name},${value[1].full_name},${value[2].full_name}, ${value[3].full_name} and ${value.length - 4} more`;
      } else {
        return value.map(e => e.full_name).join(", ");
      }
    } else {
      return ' ';
    }
  }
  task(segment, fab: FabContainer) {
    if (segment == 'create') {
      this._navCtrl.push(ManageTaskPage, { id: null, type: 'add' });
    } else if (segment == 'copy') {
      this._copyUnit();
    }
    fab.close();
  }
  taskDetail(task) {
    this._navCtrl.push(TaskDetailPage, { task_id: task.id, repeatDays: task.getTaskTime });
  }
  showNote(note) {
    let alert = this._alertCtrl.create({
      title: 'Note',
      subTitle: note,
      buttons: ['OK']
    });
    alert.present();
  }
  private _copyUnit() {
    this._shared.Loader.show();
    this._api.getAllTaskList().subscribe((data) => {
      let alert = this._alertCtrl.create();
      data.tasks.forEach(task => {
        alert.addInput({
          type: 'radio',
          label: `${task.task_name}`,
          value: task.id
        });
      });
      alert.setTitle('Select Task');
      alert.addButton('Cancel');
      this._shared.Loader.hide();
      alert.addButton({
        text: 'Copy',
        handler: data => {
          if (data) {
            this._navCtrl.push(ManageTaskPage, { id: data, type: 'copy' });
          }
        }
      });
      alert.present();
    }, (err) => {
      this._shared.Loader.hide();
      this._shared.Toast.show('Problem in fetching task list');
    });
  }
  sortTasks() {
    console.log(this.sorting)
    let alert = this._alertCtrl.create();
    this.sorting.forEach(sort => {
      alert.addInput({
        type: 'radio',
        label: `${sort.label}`,
        value: `${sort.value}`,
        checked: this.listState.sortBy == `${sort.value}`
      });
    });
    alert.setTitle('Sort Tasks');
    alert.addButton('Cancel');
    alert.addButton({
      text: 'Ok',
      handler: data => {
        if (data) {
          this.listState.page = 1;
          this.listState.sortBy = data;
          this.getTasks('', data);
        }
      }
    });
    alert.present();
  }
  filterTasks() {
   let alert = this._alertCtrl.create();
    this.filter.forEach(sort => {
      alert.addInput({
        type: 'checkbox',
        label: `${sort.label}`,
        value: `${sort.value}`,
        checked: this.listState.filter.status.indexOf(sort.value) == -1 ? false : true
      });
    });
    alert.setTitle('Filter Tasks');
    alert.addButton('Cancel');
    alert.addButton({
      text: 'Ok',
      handler: data => {
        if (data) {
          this._scrollToTop();
          this.listState.page = 1;
          this.listState.filter.status = data.map(Number);
          this.getTasks('');
        }
      }
    });
    alert.present();
  }
  getSelectedFilters() {
    if(this.listState.filter.status.length == 1) {
      let index = this.filter.map(function(o) { return o.value; }).indexOf(this.listState.filter.status[0]);
      return this.filter[index].label;
     } else if(this.listState.filter.status.length == 0) {
      return 'No Filter';
     } else {
      return 'Multiple';
    }
  

  }
}





