import { ApiProvider } from './../../providers/api/api.provider';
import { Component } from '@angular/core';
import { NavController, NavParams,ModalController } from 'ionic-angular';
import { SharedProvider } from '../../providers/shared/shared.provider';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { GroupChatPage } from '../group-chat/group-chat';
import { RespondentChatPage } from '../respondent-chat/respondent-chat';



@Component({
  selector: 'page-messages',
  templateUrl: 'messages.html',
})
export class MessagesPage {
  public list: any = {};
  public userId: number;
  public roleId:number;
  
  constructor(private _navCtrl: NavController, private _navParams: NavParams, private _shared: SharedProvider, public  modalCtrl: ModalController, private _api: ApiProvider, private _auth: AuthenticationProvider) {
    this.userId = this._auth.user.id;
    this.roleId= this._auth.userUnitRole;
 
   
    
  }

  ionViewDidLoad() {
    this.getChatList();

   
  }

  getChatList() {
    this._shared.Loader.show();
    this._api.getChatList().subscribe(response => {
      if (response['status'] == 'success') {
        this.list = response.res;
        console.log(this.list);
      }
      this._shared.Loader.hide();
    }, err => {
      console.error(err);
      this._shared.Loader.hide();
    });
  }
  openChat(id) {
    // this._navCtrl.push(GroupChatPage, { id: id });
  let modal =  this.modalCtrl.create(GroupChatPage, { id: id });
    modal.onDidDismiss(() => {
      this.getChatList();
    });
    modal.present();
  }

  openRespChat(id) {
    // this._navCtrl.push(RespondentChatPage, { id: id});
    let modal = this.modalCtrl.create(RespondentChatPage, { id: id });
    modal.onDidDismiss(() => {
      this.getChatList();
    });
    modal.present();
  }
}



