import { AuthenticationProvider } from './../../providers/authentication/authentication';
import { SharedProvider } from './../../providers/shared/shared.provider';
import { Component } from '@angular/core';
import { NavController, NavParams, AlertController } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api.provider';
import { TermsPage } from './../terms/terms';
declare function require(url: string);

@Component({
  selector: 'page-language-selection',
  templateUrl: 'language-selection.html',
})
export class LanguageSelectionPage {
  public languages: any = [];
  public selectedLanguage: any;
  public appState: any = {};
  public translatedData: any = {};
  constructor(private _navCtrl: NavController, private _shared: SharedProvider, private _api: ApiProvider, private _alert: AlertController, private _authentication: AuthenticationProvider) {
  }
  ionViewDidLoad() {
    this.translatedData = require('../../translated-data.json');
    this.getLanguage();
  }
  getLanguage() {
    this._shared.Loader.show();
    this._api.getLanguages().subscribe(response => {
      this.languages = response['language'];
      this.selectedLanguage = response['language'][0].name;
      this._shared.Loader.hide();
    }, err => {
      console.log(err);
      this._shared.Loader.hide();
    });
  }
  confirm() {
    if (!this.selectedLanguage) {
      this._shared.Toast.show('Please select a language');
      return false;
    }
    this._shared.Alert.confirm(this.translateData(this.selectedLanguage.toLowerCase() + '_language_selected'), '' + this._authentication.translation[this.selectedLanguage]['language'] + '', '' + this._authentication.translation[this.selectedLanguage]['yes'] + '', '' + this._authentication.translation[this.selectedLanguage]['change'] + '').then(() => {
      this._shared.LS.get('appState').then((data: any) => {
        if (data) {
          data.language = this.selectedLanguage;
          this._shared.LS.set('appState', data);
          this._authentication.cacheLanguage(this.selectedLanguage);
        } else {
          this.appState.language = this.selectedLanguage;
          this._shared.LS.set('appState', this.appState);
          this._authentication.cacheLanguage(this.selectedLanguage);
        }
        this._navCtrl.push(TermsPage);
      });
    }, () => {
      console.warn('User cancelled language.')
    })
  }
  translateData(value) {
    let key = value.toLowerCase().split(' ').join('_');
    if (this.translatedData) {
      if (typeof this.translatedData[this.selectedLanguage][key] == 'undefined') {
        return value;
      } else {
        return this.translatedData[this.selectedLanguage][key];
      }
    } else {
      return value;
    }
  }
}




