import { ProfilePage } from './../profile/profile';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { SharedProvider } from '../../providers/shared/shared.provider';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { ApiProvider } from './../../providers/api/api.provider';


@Component({
  selector: 'page-participants',
  templateUrl: 'participants.html',
})
export class ParticipantsPage {
  public participants: any[];
  public userRole: any;
  constructor(private _navCtrl: NavController, private _shared: SharedProvider, private _api: ApiProvider, private _authentication: AuthenticationProvider) {
    this.userRole = this._authentication.userUnitRole;
  }

  ionViewDidLoad() {
    this.getParticipants();
  }
  getParticipants() {
    console.log("DSff")
    this._shared.Loader.show();
    this._api.getParticipants().subscribe(response => {
      this._shared.Loader.hide();
      if (response.status == 1) {
        this.participants = response.getUnitParticipants;
        console.log(this.participants);
        console.log("data");
      } else {
        this._shared.Toast.show(response.msg);
      }
    }, err => {
      console.info(err);
      this._shared.Loader.hide();
    });
  }
  updateNotification(user_id, is_notify, index) {
    this._shared.Loader.show();
    this._api.updateNotificationStatus({ user_id: user_id, is_notify: is_notify, unit_id: this._authentication.unitId })
      .subscribe(response => {
        this._shared.Loader.hide();
        if (response.status == 1) {
          this.participants[index].is_notify = response.notifyStatus;
        } else {
          this._shared.Toast.show(response.msg);
        }
      }, err => {

        console.info(err);
        this._shared.Loader.hide();
      });
  }
  viewProfile(user_id) {
    if (this._authentication.userUnitRole == 2 ) {
      this._navCtrl.push(ProfilePage, {user_id:user_id, page: 'participant'});
    }
    }
}



