import { ProfilePage } from './../profile/profile';
import { ApiProvider } from './../../providers/api/api.provider';
import { Component, NgZone } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { SharedProvider } from './../../providers/shared/shared.provider';
import { AuthenticationProvider } from './../../providers/authentication/authentication';
import { UnitsPage } from './../units/units';

@Component({
  selector: 'page-manage-profile',
  templateUrl: 'manage-profile.html',
})
export class ManageProfilePage {
  public section: any = 1;
  public profile: any = {};
  public profileData: any[];
  public submitted: boolean = false;
  public iconPath = '../../assets/icon/';
  public lastImage: string = null;
  public appState: any = {};
  public profileCompletedStatus: boolean = false;
  constructor(private _navCtrl: NavController, public navParams: NavParams, private _shared: SharedProvider, private _api: ApiProvider, public _auth: AuthenticationProvider,private _zone: NgZone) {
  }

  ionViewDidLoad() {
    this.getProfileData();
    if(this._auth.profileCompleted) {
      this.profile = this._auth.user;
      this.profileCompletedStatus = true;
      this.getUserDetail();
    }
  }
  getUserDetail() {
    this._shared.Loader.show();
    this._api.getUserDetail({user_id: this._auth.user.id}).subscribe(response => {
      this.profile = response[1];
      console.log(this.profile);
      this._shared.Loader.hide();
    }, err => {
      console.info(err);
      this._shared.Loader.hide();
    });
  }
  next(data) {
    if (this.section == 1 || this.section == 2) {
      this.submitted = true;
      if (!data.valid) {
        this._shared.Toast.show(this._auth.translateData('Please fill all mandatory fields'));
        return
      } else {
        this.submitted = false;
        this.section += 1;
      }
    } else {
      this.section += 1;
    }
  }
  saveSingleItem(id, keyName) {
    if (typeof this.profile[keyName] == 'undefined') {
      this.profile[keyName] = [];
      this.profile[keyName].push(id);
    } else {
      this.profile[keyName] = [];
      this.profile[keyName].push(id);
    }
  }
  back() {
    this.section -= 1;
  }
  getProfileData() {
    this._shared.Loader.show();
    this._api.getProfileData().subscribe(response => {
      this.profileData = response;
      console.log("adf")
      console.log(this.profileData);
      this._shared.Loader.hide();
    }, err => {
      console.info(err);
      this._shared.Loader.hide();
    });
  }
  getCheckedStatus(id, keyName) {
   if(this.profile[keyName] != null) {
     if (typeof this.profile[keyName] != 'undefined') {
        return this.profile[keyName].indexOf(id) == -1 ? 'false' : 'true';
      }
    } else {
      return false;
    }
   }
  datachanged(e: any, keyName, id) {
    if (typeof this.profile[keyName] == 'undefined' || this.profile[keyName] == null) {
      this.profile[keyName] = [];
    }
    if (e.checked == true) {
      this.profile[keyName].push(id);
    } else {
      let index = this.profile[keyName].indexOf(id);
      if (index > -1) {
        this.profile[keyName].splice(index, 1);
      }
    }
  }
  saveData() {
    this.profile.user_id = this._auth.user.id;
    this._shared.Loader.show();
    this._api.saveProfileData(this.profile)
      .subscribe(response => {
        this._shared.Loader.hide();
        if (response.status == 1) {
          let userData = response.user;
          userData.token = this._auth.token;
          this.appState.user = userData;
          this.appState.profileCompleted = true;
          this._shared.LS.get('appState').then((data: any) => {
            if (data) {
              data.user = userData;
              data.profileCompleted = true;
              this._shared.LS.set('appState', data);
            } else {
              this.appState.user = response.user;
              this._shared.LS.set('appState', this.appState);
            }
            this._auth.cacheProfileCompletedState(true);
          });
          this._auth.cacheUserData(userData);
          if( this.profileCompletedStatus) {
            this._zone.run(() => {
              this._navCtrl.popTo(ProfilePage);
          });
          } else {
            this._navCtrl.setRoot(UnitsPage); 
          }
          } else {
          this._shared.Toast.show(response['msg']);
        }
      }, err => {
        console.info(err);
        this._shared.Loader.hide();
      })
  }

}



