import { ManageTaskPage } from './../manage-task/manage-task';
import { ResponsePage } from './../response/response';
import { SearchUsersPage } from './../search-users/search-users';
import { ApiProvider } from './../../providers/api/api.provider';
import { Component, NgZone } from '@angular/core';
import { NavController, NavParams, ActionSheetController, AlertController, ModalController } from 'ionic-angular';
import { SharedProvider } from './../../providers/shared/shared.provider';
import { AuthenticationProvider } from '../../providers/authentication/authentication';

@Component({
  selector: 'page-task-detail',
  templateUrl: 'task-detail.html',
})
export class TaskDetailPage {
  public task_id: number = null;
  public task: any = {};
  public isRespondedStatusSet: boolean = false;
  public repeatDays: any;
  public selectedDay: any = { status: null, id: null };
  constructor(public ngZone: NgZone, private _navCtrl: NavController, private _navParams: NavParams, private _actionSheetCtrl: ActionSheetController, private _shared: SharedProvider, private _api: ApiProvider, private _auth: AuthenticationProvider, private _alertCtrl: AlertController, private _modalCtrl: ModalController, private _zone: NgZone) {
    this.task_id = this._navParams.get("task_id");
    this.repeatDays = this._navParams.get('repeatDays');
    console.log(this.repeatDays);
  }

  ionViewDidLoad() {
    if (this.repeatDays.length > 1) { 
      this.repeatDays.every((element) => {
        console.log(element);
        if (element.status == 1) {
          this.selectedDay = element;
          return false;
        }
      });
    }
    if (!this.selectedDay.id) {
      this.selectedDay = Object.assign({}, this.repeatDays[0]);;
    }
    console.log(this.selectedDay);
    this.getTaskDetails();
  }
  getTaskDetails() {
    this._shared.Loader.show();
    this._api.getTaskDetails({ task_id: this.task_id, role_id: this._auth.userUnitRole, task_date_id: this.selectedDay.id }).subscribe(response => {
      console.log(response);
      if (response.status == 1) {
        this.task = response.tasks[0];
        if (this.task.userResponse.length > 0) {
          this.task.userResponse.map(e => {
            e.limitTo = 1;
            e.show = 'true';
          });
          this.getUserRespondedStatus();
          console.log(this.task.userResponse);
        }
      } else {
        console.log(response.msg);
      }
      this._shared.Loader.hide();
    }, err => {
      console.info(err);
      this._shared.Loader.hide();
    });
  }
  getRespondents(value) {
    if (value.length > 0) {
      if (value.length > 4) {
        return `${value[0].full_name},${value[1].full_name},${value[2].full_name}, ${value[3].full_name} and ${value.length - 4} more`;
      } else {
        return value.map(e => e.full_name).join(", ");
      }
    } else {
      return ' ';
    }
  }
  showNote(note) {
    let alert = this._alertCtrl.create({
      title: 'Note',
      subTitle: note,
      buttons: ['OK']
    });
    alert.present();
  }

  deleteResponseReactionAlert(id, segment) {
    let actionSheet = this._actionSheetCtrl.create({
      title: 'Action',
      buttons: [
        {
          text: 'Delete',
          handler: () => {
            this._shared.Alert.confirm('', 'Delete ' + segment + '').then(() => {
              this.deleteResponseReactions({ has_response_id: id });
            }, () => {
              console.warn('User cancelled action.')
            })
          }
        }, {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }
  taskAlert() {
    let actionSheet = this._actionSheetCtrl.create({
      title: 'Action',
      buttons: [
        {
          text: 'Edit',
          handler: () => {
            this._shared.Alert.confirm('This task will be marked as Cancelled and a copy of this task will be available with modified information. Do you want to continue?', 'Confirmation', 'Yes').then(() => {
              this._navCtrl.push(ManageTaskPage, { id: this.task.id, type: 'edit' });
            }, () => {
              console.warn('User cancelled.')
            })
          }
        },
        {
          text: 'Copy',
          handler: () => {
            this._navCtrl.push(ManageTaskPage, { id: this.task.id, type: 'copy' });
          }
        }, {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }
  addResponse() {
    let data = { task_id: this.task.id, role_id: this._auth.userUnitRole, task_date_id: this.task.getTaskTime[0].id, has_response_id: 0, task_user_id: this._auth.user.id }; //fix
    let modal = this._modalCtrl.create(ResponsePage, { segment: 'response', data: data });
    modal.onDidDismiss(data => {
      if (data.refresh) {
        this.getTaskDetails();
      }
      console.info(data);
    })
    modal.present();
  }
  addReaction(id) {
    let data = { task_id: this.task.id, role_id: this._auth.userUnitRole, task_date_id: this.task.getTaskTime[0].id, has_response_id: id }; //fix

    let modal = this._modalCtrl.create(ResponsePage, { segment: 'react', data: data }, { cssClass: "react-modal" });
    modal.onDidDismiss(data => {
      if (data.refresh) {
        this.getTaskDetails();
      }
      console.info(data);
    });
    modal.present();
  }
  allParticipants() {
    let data = this.task.respondedUser.concat(this.task.nonRespondedUser);
    let modal = this._modalCtrl.create(SearchUsersPage, { source: 'task-detail', users: data });
    modal.present();
  }
  showAllReactions(action, i) {
    if (action == 'show') {
      let arrayLength = this.task.userResponse[i].reaction.length;
      this.task.userResponse[i].limitTo = arrayLength - 1;
      this.task.userResponse[i].show = 'true';
    } else {
      this.task.userResponse[i].limitTo = 1;
      this.task.userResponse[i].show = 'false';
    }
  }
  getUserRespondedStatus() {
    if (!this.isRespondedStatusSet) {
      this.task.userResponse.map(e => {
        if (e.task_user_id == this._auth.user.id) {
          this.isRespondedStatusSet = true;
        }
      });
    }
  }
  deleteResponseReactions(data) {
    this._shared.Loader.show();
    this._api.deleteResponseReactions(data).subscribe(response => {
      if (response.status == 0) {
        this.isRespondedStatusSet = false;
        response.has_response_id == 0 ? this._shared.Toast.show('Response has been deleted successfully.') : this._shared.Toast.show('Reaction has been deleted successfully.')
        this.getTaskDetails();
      } else {
        this._shared.Toast.show('Something went wrong. Please try again.');
      }
      this._shared.Loader.hide();
    }, err => {
      console.info(err);
      this._shared.Loader.hide();
    });
  }

}



