import { Injectable } from '@angular/core';
import { ToastController, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage'; 
import { ApiProvider } from '../api/api.provider'; 
import { SpinnerDialog } from '@ionic-native/spinner-dialog';
 

@Injectable()
export class SharedProvider { 
  private _toastMsg; 

  constructor(private _spinnerDialog: SpinnerDialog, private _toastCtrl: ToastController, private _storage: Storage, private _alert: AlertController) { }
 
  public Loader = {
    show: (template?, showBackdrop?) => {
      this._spinnerDialog.show(null, null, showBackdrop || true); 
    },
    hide: () => {
      setTimeout(() => { 
        this._spinnerDialog.hide();
      }, 100);
    }
  }
  // activeLister() { 
  //   let interval; 
  //   interval = setInterval(() => {
  //     console.log('removed by interval');
  //     this.Loader.hide();
  //     clearInterval(interval);
  //   }, 3000);
  // }
  public Toast = {
    show: (text: string, duration?, position?, closeButton?, btnText?) => {
      this._toastMsg = this._toastCtrl.create({
        message: text,
        duration: duration || closeButton ? null : 3000,
        position: position || 'top',
        showCloseButton: closeButton || false,
        closeButtonText: btnText || 'OK'
      });
      this._toastMsg.present();
    },
    hide() {
      this._toastMsg.dismiss();
    }
  }
  public LS = {
    get: (key: string) => {
      return this._storage.get(key);
    },
    set: (key: string, value) => {
      return this._storage.set(key, value);
    },
    remove: (key: string) => {
      return this._storage.remove(key);
    }
  }
  public Alert = {
    confirm: (msg?, title?, okLabel?, cancelLabel?) => {
      return new Promise((resolve, reject) => {
        let alert = this._alert.create({
          title: title || 'Confirm',
          message: msg || 'Do you want continue?',
          buttons: [
            {
              text: cancelLabel || 'Cancel',
              role: 'cancel',
              handler: () => {
                reject(false);
              }
            },
            {
              text: okLabel || 'Ok',
              handler: () => {
                resolve(true);
              }
            }
          ]
        });
        alert.present();
      });

    },
    alert: (msg, title?) => {
      let alert = this._alert.create({
        title: title || 'Alert',
        subTitle: msg,
        buttons: ['Dismiss']
      });
      alert.present();
    }
  } 
 
}



