import { UnitsPage } from './../units/units';
import { AuthenticationProvider } from './../../providers/authentication/authentication';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { ManageProfilePage } from '../manage-profile/manage-profile';
import { SharedProvider } from '../../providers/shared/shared.provider';

@Component({
    selector: 'page-confirm-identity',
    templateUrl: 'confirm-identity.html',
})
export class ConfirmIdentityPage {
    public full_name: string;
    public userData: any = {};
    public submitted: boolean = false;
    constructor(private _navCtrl: NavController, public navParams: NavParams, private _auth: AuthenticationProvider, private _shared: SharedProvider) {
    }

    ionViewDidLoad() {
        this.full_name = this._auth.user.full_name;
        this.maskName( this.full_name );
    }
    create() {
        this._navCtrl.push(ManageProfilePage);
    }
    confirm(data) {
        this.submitted = true;
        if (!data.valid) {
            this._shared.Toast.show(this._auth.translateData('Please fill all mandatory fields'));
            return
        } else {
            if (this.userData.dob == this._auth.user.dob && this.userData.gender == this._auth.user.gender) {
                this._shared.LS.get('appState').then((data: any) => {
                 data.profileCompleted = true; 
                this._shared.LS.set('appState', data);
                 this._auth.cacheProfileCompletedState(true);
                });
                this._navCtrl.setRoot(UnitsPage);
            } else {
                this._shared.Toast.show('Provided information did not match with the existing information.', null, null, true);
            }
        }
    }

    maskName(name) {
        let fullName: string = '';
        let splitNameArr = name.split(' ');
        for (let j = 0; j < splitNameArr.length; j++) {

            let length = splitNameArr[j].length;
            let nameArr = [];
            for (let i = 0; i < length; i++) {
                if (i > 0) {
                    nameArr[i] = splitNameArr[j][i].replace(/./g, "*");
                }
                else {
                    nameArr[i] = splitNameArr[j][i];
                }
            }
            let maskedName = "";
            for (let i = 0; i < length; i++) {
                maskedName += nameArr[i];
            }
            fullName = fullName + ' ' + maskedName;
        }

        this.full_name = fullName;

    }
}



