import { SharedProvider } from './../../providers/shared/shared.provider';
import { Component } from '@angular/core';
import { NavController, NavParams, ViewController } from 'ionic-angular';
import { dummyTask, TokenProvider } from '../../providers/config';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { ApiProvider } from '../../providers/api/api.provider';
import { TasksPage } from '../tasks/tasks';


@Component({
  selector: 'page-task-preview',
  templateUrl: 'task-preview.html',
})
export class TaskPreviewPage {
  public task: any = { access: [], taskDetail: {}, users: [] };
  // public task: any = dummyTask;
  constructor(private _shared: SharedProvider, private _api: ApiProvider, private _auth: AuthenticationProvider, private _navCtrl: NavController, private _navParams: NavParams, private _viewCtrl: ViewController, private _config: TokenProvider) {
    this.task = this._navParams.get('data');
    console.log(JSON.stringify(this.task));
  }
  ionViewDidLoad() {
  }
  prepareTask() {
    let taskDetail = this.task.taskDetail;

    this.task.access.forEach(element => {
      if (element.status == 0) {
        taskDetail.attachment_notrequired.push(element.id);
      } else if (element.status == 1) {
        taskDetail.attachment_optional.push(element.id);
      } else {
        taskDetail.attachment_required.push(element.id);
      }
    });

    if (taskDetail.assign_to == 1) {
      taskDetail['user_id'] = [];
      this.task.users.forEach(user => {
        taskDetail.user_id.push(user.id);
      });
    }
    taskDetail.unit_id = this._auth.unitId;
    taskDetail.role_id = this._auth.userUnitRole;

    delete taskDetail.task_attachments //Remove later

    console.log(taskDetail);
    this.saveTask(taskDetail);
  }
  saveTask(taskDetail) { 
    this._shared.Loader.show();
    this._api.insertTask(taskDetail).subscribe(response => {
      this._shared.Loader.hide();
      if (response.task.id) {
        this._shared.Toast.show('Your task has been created successfully.');
        this._config.toggleTaskAdded(response.task.id);
        this._viewCtrl.dismiss({taskAdded: true});
      }
    }, err => {
      this._shared.Loader.hide();
      console.error(err);
      this._shared.Toast.show('Oops! something went wrong.');
    })
  }
  getClass(status) {
    if (status == 0) {
      return 'action-box red hydrated';
    } else if (status == 1) {
      return 'action-box yellow hydrated';
    } else {
      return 'action-box green hydrated';
    }
  }
  edit() {
    this._viewCtrl.dismiss({ page: 1, taskAdded: false});
  }
  dismiss() {
    this._viewCtrl.dismiss({taskAdded: false});
  }
}



