import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { baseURL, TokenProvider } from '../config';
import 'rxjs/Rx';
import { AuthenticationProvider } from './../authentication/authentication';

@Injectable()
export class ApiProvider {
  constructor(public http: Http, public token: TokenProvider, private _auth: AuthenticationProvider) { }
  getLanguages(): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.get(`${baseURL}/get-language`, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getTerms(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.post(`${baseURL}/terms-conditions`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getCountries(): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.get(`${baseURL}/get-country`, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getUnitList(): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/get-units`, { userId: this._auth.user.id }, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }

  registerDevice(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/register-device`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getParticipants(): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/get-unit-participant`, { unitId: this._auth.unitId }, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  saveRegistration(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.post(`${baseURL}/login`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  checkOtp(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.post(`${baseURL}/check-otp`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getProfileData(): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.get(`${baseURL}/profile-info`, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  resendOtp(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.post(`${baseURL}/resend-otp`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  saveProfileData(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/save-profile`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getUserDetail(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/view-profile`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getChatList() {
    console.log(baseURL);
    let data = { unit_id: this._auth.unitId };
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/get-msg-groups`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  updateNotificationStatus(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/is-notify`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getGroupChatDetail(data) {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/get-group-chat`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getTaskList(state): Observable<any> {
    // , filter: state.filter
    let data = { unit_id: this._auth.unitId, role_id: this._auth.userUnitRole, sort: state.sortBy, page: state.page };
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/task/get-tasks`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getAllTaskList(): Observable<any> {
    let data = { unit_id: this._auth.unitId, role_id: this._auth.userUnitRole, page: 0};
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/task/get-tasks`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getTranslation(): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.get(`${baseURL}/get-translation`, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }

  sendChat(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/add-msg`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  insertTask(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/task/add-edit`, data, { headers: headers }).map((response: Response) => {
      return response.json();
    })
      .catch(this.handleError);
  }
  insertReaction(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/task/task-reaction`, data, { headers: headers }).map((response: Response) => {
      return response.json();
    })
      .catch(this.handleError);
  }
  insertResponse(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/task/task-response`, data, { headers: headers }).map((response: Response) => {
      return response.json();
    })
      .catch(this.handleError);
  }
  sendNotification(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/send-notification`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getUnitUsers(): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/task/get-unit-user`, { unit_id: this._auth.unitId }, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }

  getRespondentChat(data) {
    //let data = { unitId: this._auth.unitId };
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/get-moderator-resp-chat`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }

  getTaskDetails(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/task/get-task-details`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  getTask(id) {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/task/get-single-task`, { task_id: id }, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  deleteResponseReactions(data): Observable<any> {
    let headers = new Headers({ 'Content-Type': 'application/json', 'token': this._auth.token });
    return this.http.post(`${baseURL}/task/delete-response-reaction`, data, { headers: headers })
      .map((response: Response) => {
        return response.json();
      })
      .catch(this.handleError);
  }
  handleError(error) {
    console.error(error);
    return Observable.throw(error || 'Server error');
  }

}



