import { ManageProfilePage } from './../manage-profile/manage-profile';
import { SharedProvider } from './../../providers/shared/shared.provider';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api.provider';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { CallNumber } from '@ionic-native/call-number';


@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {
  public user: any = {};
  public userData = [];
  public user_id: number = null;
  public page: string = null;
  public loggedInUser: number;
  public iconPath = '../../assets/icon/';
  constructor(private _navCtrl: NavController, private _navParams: NavParams, private _shared: SharedProvider, private _api: ApiProvider, private _auth: AuthenticationProvider, private _callNumber: CallNumber) {
    this.user_id = this._navParams.get("user_id");
    this.page = this._navParams.get("page");
  }
  ionViewDidEnter() {
    this.loggedInUser = this._auth.user.id;
    this.getUserDetail();
    console.log("ionViewDidEnter"); 
  }
  getUserDetail() {
    this._shared.Loader.show();
    this._api.getUserDetail({user_id: this.user_id}).subscribe(response => {
      this.user = response[1];
      this.userData = response.data;
      console.info(this.user)
      this._shared.Loader.hide();
    }, err => {
      console.info(err);
      this._shared.Loader.hide();
    });
  }
  ManageProfile() {
   this._navCtrl.push(ManageProfilePage);
  }
  callNumber(number) {
    this._callNumber.callNumber(number, true);
  }
}



