import { AuthenticationProvider } from './../providers/authentication/authentication';
import { Pipe, PipeTransform } from '@angular/core';
declare function require(url: string);
@Pipe({
  name: 'appLevelTranslation',
})
export class AppLevelTranslation implements PipeTransform {
  public translatedData: any = {};
  constructor(private _auth: AuthenticationProvider) {
    this.translatedData = require('../translated-data.json');
  }
  transform(value: string, ...args) {
    let key = value.toLowerCase().split(' ').join('_');
    if (this.translatedData) {
      if (typeof this.translatedData[this._auth.language][key] == 'undefined') {
        return value;
      } else {
        return this.translatedData[this._auth.language][key];
      }
    } else {
      return value;
    }
  }
}


