import { AuthenticationProvider } from './../../providers/authentication/authentication';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { SharedProvider } from './../../providers/shared/shared.provider';
import { ApiProvider } from '../../providers/api/api.provider';
import { NotActiveUnitPage } from './../not-active-unit/not-active-unit';
import { OtpPage } from './../otp/otp';
@Component({
  selector: 'page-registration',
  templateUrl: 'registration.html',
})
export class RegistrationPage {
  public countries: any = [];
  public registrationForm: any = { country: '91' };
  public submitted: boolean = false;
  constructor(public navCtrl: NavController, public navParams: NavParams, public _shared: SharedProvider, private _api: ApiProvider, private _auth: AuthenticationProvider) {
  }

  ionViewDidLoad() {
    this.getCountries();
    console.log(this._auth.language);
  }
  getCountries() {
    this._shared.Loader.show();
    this._api.getCountries().subscribe(response => {
      this.countries = response;
      this._shared.Loader.hide();
    }, err => {
      console.info(err);
      this._shared.Loader.hide();
    });
  }

  sendOtp(data) {
    this.submitted = true;
    if (!data.valid) {
      this._shared.Toast.show(this._auth.translateData('Please fill all mandatory fields'));
      return
    } else {
      this._shared.Loader.show();
      this._api.saveRegistration(this.registrationForm)
        .subscribe(response => {
          this._shared.Loader.hide();
          if (response['status'] == 1) {
            this.navCtrl.push(OtpPage, { id: response.user.id, otp: response.otp, mobile: this.registrationForm.mobile });
          } else {
            this.navCtrl.push(NotActiveUnitPage);
          }

        }, err => {
          console.info(err);
          this._shared.Loader.hide();
        });
    }
  }
  // getLabel(value) {
  //   let key = value.toLowerCase().split(' ').join('_');
  //   if (typeof this.labelText[this._auth.language.toLowerCase()][key] == 'undefined') {
  //     return value;
  //   } else {
  //     return this.labelText[this._auth.language.toLowerCase()][key];
  //   }
  // }
}



