import { Component, ElementRef, ViewChild } from '@angular/core';
import { NavParams, NavController } from 'ionic-angular';
import { Events, Content, ViewController } from 'ionic-angular';
import { Socket } from 'ng-socket-io';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { ApiProvider } from './../../providers/api/api.provider';
import { SharedProvider } from '../../providers/shared/shared.provider';


import { Observable } from 'rxjs/Observable';

import { ChatService, ChatMessage, UserInfo } from "../../providers/chat-service";

@Component({
  selector: 'page-group-chat',
  templateUrl: 'group-chat.html',
})
export class GroupChatPage {

  @ViewChild(Content) content: Content;
  @ViewChild('chat_input') messageInput: ElementRef;
  msgList: ChatMessage[] = [];
  user: UserInfo;
  toUser: UserInfo;
  editorMsg = '';
  showEmojiPicker = false;
  public msgGroup = [];
  public userId: number;
  private _unitId: number;

  public fromId: number;
  public fullName: string;
  public toId: number;
  public message: any;
  public title: any;
  // public content:any;
  public messages = [];
  public msgGrpId: number;
  public notifyTitle: any;
  public room:any;


  constructor(private chatService: ChatService, private socket: Socket, public navCtrl: NavController, private shared: SharedProvider, private _auth: AuthenticationProvider, private _api: ApiProvider, public navParams: NavParams, private events: Events, private _viewCtrl: ViewController) {
    // Get the navParams toUserId parameter
    this.msgGrpId = this.navParams.get('id');
    console.log('inside group chat detail in constructor');
   
    //this.msgGrpId= 1; 
    this.userId = this._auth.user.id;
    this.fromId = this._auth.user.id;
    this._unitId = this._auth.unitId;
    this.fullName = this._auth.user.full_name;
    this.room = this._unitId+"_"+this.msgGrpId;

    console.log('unitid---'+this._unitId);
    console.log('room=========ghfgh========');
    console.log(this.room);
    console.log('room=========fghfgh========');
    this.getMessages().subscribe(message => {
    console.log(message);

      this.messages.push(message);
      //this.content.scrollBottom();

    });

  }

  ionViewDidLoad() {

  }
 

  sendMessage() {
    console.log('inside send');
    var currentdate = new Date();
    var datetime = currentdate;
    this.socket.emit('add-message', { msg: this.message,room:this.room ,unit_id: this._unitId, from_id: this.fromId, to_id: this.toId, msg_group_id: this.msgGrpId, created_at: datetime, full_name: this.fullName });
    let data = { msg: this.message, unit_id: this._unitId,room:this.room , from_id: this.fromId, to_id: this.toId, msg_group_id: this.msgGrpId, created_at: datetime, full_name: this.fullName };
    this.notifyTitle = 'from respondent';
    this._api.sendChat(data)
      .subscribe(res => {
        if (res['status'] == 'success') {
        }
      }, err => {
        console.log(err);

      });

    this.message = '';
    //  this.content.scrollBottom();
    this.scrollToBottom();

    //this.message
  }
  getMessages() {
    // if (this.message) {
    let observable = new Observable(observer => {
      this.socket.on('message', (data) => {
        console.log(data);
        observer.next(data);
      });
    })
    return observable;
    //}
    // this.scrollToBottom();

  }
  ngOnInit() {
    console.log('inside ng init');
     this.socket.connect();

    let data = { unit_id: this._unitId, msg_group_id: this.msgGrpId };
    console.log(data);
    this._api.getGroupChatDetail(data)
      .subscribe(res => {
        if (res['status'] == 'success') {
          this.title = res.title;
          this.messages = res.groupchat;
          this.scrollToBottom();
        }
        this.shared.Loader.hide();
      }, err => {
        this.shared.Loader.hide();
      });


  }
  
  ionViewWillLeave() {
    // unsubscribe
    this.events.unsubscribe('chat:received');
    this.socket.disconnect();
  }



  onFocus() {
    this.showEmojiPicker = false;
    this.content.resize();
    //this.scrollToBottom();
  }


  getMsg() {
    // Get mock message list
    return this.chatService
      .getMsgList()
      .subscribe(res => {
        this.msgList = res;
        //   this.scrollToBottom();
      });
  }

 

  getMsgIndexById(id: string) {
    return this.msgList.findIndex(e => e.messageId === id)
  }

  scrollToBottom() {
    setTimeout(() => {
      if (this.content.scrollToBottom) {
        this.content.scrollToBottom();
      }
    }, 400)
  }

  private focus() {
    if (this.messageInput && this.messageInput.nativeElement) {
      this.messageInput.nativeElement.focus();
    }
  }

  private setTextareaScroll() {
    const textarea = this.messageInput.nativeElement;
    textarea.scrollTop = textarea.scrollHeight;
  }
  dismiss() {
    this._viewCtrl.dismiss();
  }
}



