import { ApiProvider } from './../../providers/api/api.provider';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { SharedProvider } from '../../providers/shared/shared.provider';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { TasksPage } from '../tasks/tasks';

@Component({
  selector: 'page-units',
  templateUrl: 'units.html',
})
export class UnitsPage {
  public units: any = [];
  public selectedUnitId: number = null ;
  constructor(private _shared: SharedProvider, private _api: ApiProvider, private _authentication: AuthenticationProvider, private _navCtrl: NavController, private _navParams: NavParams) {
  }

  ionViewDidLoad() {
    this.selectedUnitId = this._authentication.unitId;
    this.getUnits();
   }
  getUnits() {
    this._shared.Loader.show();
    this._api.getUnitList().subscribe(response => {
      this._shared.Loader.hide(); 
      if (response['status'] == 1) {
        this.units = response['units'];
      }
    }, err => {
      console.log(err);
      this._shared.Loader.hide();
    });
  }
  async getTasks(unitId, i) { 
     let data = await this._shared.LS.get('appState');
      data.unitId = unitId;
      data.unitName = this.units[i].unit_name;
      data.userUnitRole = this.units[i].role_id;
      this._shared.LS.set('appState',data);
      this._authentication.cacheUnitId(unitId);
      this._authentication.cacheUnitName(this.units[i].unit_name, this.units[i].role_id);
      this._navCtrl.setRoot(TasksPage, {unitId: unitId}); 
    } 
}



