import { TaskDetailPage } from './../pages/task-detail/task-detail';
import { SearchUsersPage } from './../pages/search-users/search-users';
import { ChatService } from './../providers/chat-service';
import { GroupChatPage } from './../pages/group-chat/group-chat';
import { RespondentChatPage } from './../pages/respondent-chat/respondent-chat';

import { MessagesPage } from './../pages/messages/messages';
import { ConfirmIdentityPage } from './../pages/confirm-identity/confirm-identity';
import { SpinnerDialog } from '@ionic-native/spinner-dialog';
import { CallNumber } from '@ionic-native/call-number';
import { LanguageSelectionPage } from './../pages/language-selection/language-selection';
import { TermsPage } from './../pages/terms/terms';
import { RegistrationPage } from './../pages/registration/registration';
import { OtpPage } from './../pages/otp/otp';
import { ManageProfilePage } from './../pages/manage-profile/manage-profile';
import { ProfilePage } from './../pages/profile/profile';
import { UnitsPage } from './../pages/units/units';
import { TasksPage } from './../pages/tasks/tasks';
import { SharedProvider } from './../providers/shared/shared.provider';
import { TokenProvider } from './../providers/config';
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { IonicStorageModule } from '@ionic/storage'; 
import { NotActiveUnitPage } from './../pages/not-active-unit/not-active-unit';
import { ParticipantsPage } from './../pages/participants/participants';
import { MyApp } from './app.component'; 

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { ApiProvider } from '../providers/api/api.provider';
import { HttpModule } from '@angular/http';
import { IdentityFoundPage } from '../pages/identity-found/identity-found';
import { AuthenticationProvider } from '../providers/authentication/authentication';
import { RelativeTime } from '../pipes/relative-time';
import { HttpClientModule } from '@angular/common/http'; 
import { SocketIoModule, SocketIoConfig } from 'ng-socket-io';
import { Translate } from '../pipes/translate';
import { ManageTaskPage } from '../pages/manage-task/manage-task';
import { AppLevelTranslation } from './../pipes/app-level-translation';
import { TaskPreviewPage } from '../pages/task-preview/task-preview';
import { IonicImageViewerModule } from 'ionic-img-viewer';

const config: SocketIoConfig = { url: 'http://dragonflychat.infini.work/', options: {} };
import { OneSignal } from '@ionic-native/onesignal';
import { ResponsePage } from '../pages/response/response';


@NgModule({
  declarations: [
    MyApp, 
    LanguageSelectionPage,
    TermsPage,
    RegistrationPage,
    OtpPage,
    ManageProfilePage,
    ProfilePage,
    UnitsPage,
    TasksPage,
    NotActiveUnitPage,
    IdentityFoundPage,
    ConfirmIdentityPage,
    TasksPage,
    MessagesPage,
    RespondentChatPage,
    GroupChatPage,
    RelativeTime,
    Translate,
    ParticipantsPage,
    ManageTaskPage,
    AppLevelTranslation,
    SearchUsersPage,
    TaskPreviewPage,
    TaskDetailPage,
    ResponsePage
  ],
  imports: [
  BrowserModule,
    IonicModule.forRoot(MyApp, { backButtonIcon: 'ios-arrow-back',scrollAssist: false, autoFocusAssist: false}),
    HttpModule,
    HttpClientModule,
    IonicStorageModule.forRoot(),
    SocketIoModule.forRoot(config),
    IonicImageViewerModule
    
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp, 
    LanguageSelectionPage,
    TermsPage,
    RegistrationPage,
    OtpPage,
    ManageProfilePage,
    ProfilePage,
    UnitsPage, 
    NotActiveUnitPage,
    IdentityFoundPage,
    ConfirmIdentityPage,
    TasksPage,
    MessagesPage,
    GroupChatPage,
    ParticipantsPage,
    ManageTaskPage,
    RespondentChatPage,
    SearchUsersPage,
    TaskPreviewPage,
    TaskDetailPage,
    ResponsePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    ApiProvider,
    TokenProvider,
    SharedProvider,
    SpinnerDialog, 
    CallNumber,
    OneSignal,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthenticationProvider,
    ChatService
  ]
})
export class AppModule {}



