import { TaskPreviewPage } from './../task-preview/task-preview';
import { SearchUsersPage } from './../search-users/search-users';
import { Component, ViewChild, NgZone } from '@angular/core';
import { NavController, NavParams, ModalController, AlertController } from 'ionic-angular';
import { SharedProvider } from '../../providers/shared/shared.provider';
import { ApiProvider } from '../../providers/api/api.provider';


@Component({
  selector: 'page-manage-task',
  templateUrl: 'manage-task.html',
})
export class ManageTaskPage {
  public section = 1;
  public taskDetail: any = { attachment_required: [], attachment_optional: [], attachment_notrequired: [], task_attachments: {}, is_public: 1, frequency: 2, assign_to: 2 };
  public id: any = null;
  public errorMsg = null;
  private _type: string;
  public access = [
    { 'icon': 'image', id: 1, 'name': 'Add Image', 'status': 0 },
    { 'icon': 'mic', id: 2, 'name': 'Add Audio', 'status': 0 },
    { 'icon': 'videocam', id: 3, 'name': 'Add Video', 'status': 0 },
    { 'icon': 'pin', id: 4, 'name': 'Add Location', 'status': 0 },
    { 'icon': 'document', id: 5, 'name': 'Add File', 'status': 0 }
  ];
  public today = new Date().toJSON().split('T')[0];

  public selectedUsers: any = [];
  public submitted: boolean = false;
  @ViewChild('taskForm') form: any;
  constructor(private _shared: SharedProvider, private _api: ApiProvider, public navCtrl: NavController, public navParams: NavParams, private _modalCtrl: ModalController, private _alert: AlertController) {
    this.id = this.navParams.get('id');
    this._type = this.navParams.get('type');
  }
  ionViewDidLoad() {
    if (!this.id) {
      this.getUnitRespondets();
    } 
    // setTimeout(() => {
    //   this.taskDetail.start_date = this.today;
    //   console.log(this.today);
    // }, 1000);
    if (this.id) {
      this.getTaskDetail();
    }
  }
  getTaskDetail() {
    this._shared.Loader.show();
    this._api.getTask(this.id).subscribe((data) => {
      this.prepareTask(data.task);
      delete this.taskDetail.start_date;
      delete this.taskDetail.start_time;
      this._shared.Loader.hide();
    }, (err) => {
      this._shared.Loader.hide();
      alert('Oops! something went wrong.');
      this.navCtrl.pop();
    });
  }
  next() {
    this.submitted = false;
     if (!this.form.valid) {
      this.submitted = true;
      return false;
    }
    if (this.section < 6) {
     
      if(this.section == 4 ) {
      if(this.taskDetail.expiry_time < this.taskDetail.due_time) {
        this._shared.Toast.show('Expiry hours should not be less than due hours.');
        return;
       }
      }
      this.section += 1;
    } else {
      return;
    }
  }
  back() {
    if (this.section != 1) {
      this.section -= 1;
    }
  }
  changeStatus(index) {
    if (this.access[index].status == 0 || this.access[index].status == 1) {
      this.access[index].status += 1;
    } else if (this.access[index].status == 2) {
      this.access[index].status = 0;
    } else {
      return;
    }
  }
  getClass(status) {
    if (status == 0) {
      return 'action-box red hydrated';
    } else if (status == 1) {
      return 'action-box yellow hydrated';
    } else {
      return 'action-box green hydrated';
    }
  }
  selectRespondent() {
    let modal = this._modalCtrl.create(SearchUsersPage, { source: 'manage-task' });
    modal.onDidDismiss((data) => {
      try {
        if (data) {
          this.selectedUsers = data.users;
          console.log(this.selectedUsers);
        }
      } catch (e) {
        console.error(e);
      }
    });
    modal.present();
  }
  removeUser(index) {
    this.selectedUsers.splice(index, 1);
  }
  updateAccess(element, value) {
    let index = this.access.findIndex(x => x.id == element);
    if (index > -1) {
      this.access[index].status = value;
    }
  }
  prepareTask(task) {
    task.user_id = [];
    task.assign_to = 2;
    task.task_attachments = []; //Remove later 

    let dateTime = task.sdatetime.split(' ');
    task.start_time = dateTime[1];

    if (task.attachment_notrequired instanceof Array) {
      task.attachment_notrequired.forEach(element => {
        this.updateAccess(element, 0);
      });
    } else {
      task.attachment_notrequired = [];
    }
    if (task.attachment_optional instanceof Array) {
      task.attachment_optional.forEach(element => {
        this.updateAccess(element, 1)
      });
    } else {
      task.attachment_optional = [];
    }
    if (task.attachment_required instanceof Array) {
      task.attachment_required.forEach(element => {
        this.updateAccess(element, 2)
      });
    } else {
      task.attachment_required = [];
    }
    if (this._type == 'copy') {
      delete task.id;
    }
    this.taskDetail = task;
  }
  preview() {
    let modal = this._modalCtrl.create(TaskPreviewPage, { data: { taskDetail: this.taskDetail, access: this.access, users: this.selectedUsers } });
    modal.onDidDismiss((data) => {
      try {
        console.log(data);
        if (data.page) {
          this.section = 1;
        };
        if (data.taskAdded == true) {
          this.navCtrl.popToRoot();
        }
      } catch (e) {
        console.info(e);
      }
    });
    modal.present();
  }
  getUnitRespondets() {
    this._api.getUnitUsers().subscribe(data => {
      if (data.getUnitUser.length == 0) {
        this.errorMsg = 'This unit doesn\'t have any respondents';
        this.showErrorMsg();
      }
    }, err => {
      this.errorMsg = 'Oops! something went wrong.';
      this.showErrorMsg();
    });
  };
  showErrorMsg() {
    let alert = this._alert.create({
      title: 'Message',
      message: this.errorMsg,
      buttons: [
        {
          text: 'Ok',
          handler: () => {
            this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }
}



